<template>
    <div class="list_box">
        <div class="searchBox">
            <h3><span @click='goLast' class="icon-left"></span>
                <p>我的钱包</p>
            </h3>
        </div>
        <div class="bag">
            <div class="bag_inner flex">
                <div class="bag_left" @click="linkTo('/getMoney')">
                    <div class="icon-rmb"></div>
                    <p>余 额</p>
                    <p class="money">{{money|tofixTwo}}</p>
                </div>
                <div class="bag_right" @click="linkTo('/bankCard')">
                    <div class="icon-bankCard"></div>
                    <p>银行卡</p>
                </div>
            </div>
        </div>

        <div class="wallet_btn">
            <yd-button @click.native="linkTo('/withdrawals')" bgcolor="#5e1515" color="#fff" size="large" type="danger">提现记录</yd-button>
        </div>

    </div>
</template>
<script>
    import ajax from "../../ajax/ajax.js";
    import dataURL from "../../server/dataUrl.js";
    export default {
        data() {
            return {
                money: 0
            }
        },
        methods: {
            goLast() {
                this.$router.go(-1);
            },
            linkTo(adr) {
                this.$router.push({
                    path: adr
                });
            },
            async getUserinfo() {
                let res = await ajax.Get(dataURL.main.userInfo);
                this.money = res.wallte.balance;
            }
        },
        filters: {
            tofixTwo: function (value) {
                return (parseFloat(value) / 10000).toFixed(2);
            }
        },
        created() {
            this.getUserinfo();
        }
    }
</script>


